
.grid__padding--left {
  padding-left: 2px;
  box-sizing: border-box;
}
.grid__padding--right {
  padding-right: 2px;
  box-sizing: border-box;
}
.grid__padding--left--right {
  padding-left: 2px;
  padding-right: 2px;
  box-sizing: border-box;
}
select {
  width: 100%;
  height: 2.5em;
  border: 0;
  border-radius: 7px;
  background-color: white;
  border: 1px solid #e0e9fc;
  color: #7287b2;
  font-family: Montserrat;
}
select:focus {
  outline: 0;
}
select option {
  width: 50px;
}
h2 {
  margin-top: 5px;
  margin-bottom: 10px;
  font-weight: 300;
}
.configurator__input {
  font-family: Montserrat;
  color: #7287b2;
  border: 1px solid #e0e9fc;
  border-radius: 7px;
  padding-left: 10px;
  padding-bottom: 10px;
  padding-right: 10px;
  padding-top: 10px;
  margin-bottom: 5px;
  box-sizing: border-box;
  width: 100%;
}
.configurator__input--50 {
  width: 50%;
}
input::-webkit-input-placeholder {
  color: #8ca6db;
}
input:focus {
  outline: none !important;
  border:1px solid #b993d6;
  box-shadow: 0 0 3px #719ECE;
}



