
.truncate-overflow {
  width: calc(100% - 12.5%);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.grid__padding--right {
  padding-right: 2px;
  box-sizing: border-box;
}
.grid__padding--left {
  padding-left: 2px;
  box-sizing: border-box;
}



